.sidebarOption{
    display: flex;
    align-items: center;
    cursor: pointer;
}

.sidebarOption :first-child{
  padding: 20px;
  
}

.sidebarOption > h2{
    font-weight: 800;
    font-size: 20px;
    margin-right:20px;
}

.sidebarOption:hover{
    background-color:var(--twitter-background);
    border-radius: 30px;
    color:var(--twitter-color);
    transition: color 100ms ease-out;
}

.sidebarOption--active{
    color:var(--twitter-color);
}